<template>
  <div class="answer">
    <template>
      <div class="container">
        <div class="content">
          <div class="con_head">
            <div class="title">{{ seleaten.sole }}</div>
            <div class="score">
              <span>{{ active + 1 }}</span
              >/{{ mulList.length }}
            </div>
          </div>
          <div class="con_text">
            <div class="title_topic">
              {{ seleaten.topic }}
            </div>
            <!-- 单选题 区域 -->
            <div
              v-if="seleaten.sole === '单选题' || seleaten.sole === '判断题'"
            >
              <div
                v-for="(item, index) in seleaten.arr"
                :key="index"
                @click="SoleAdd(item)"
                class="con_odd"
                :class="{
                  active: seleaten.value == item.value,
                  success: seleaten.answer == item.value && seleaten.ischecked,
                  error:
                    item.value == seleaten.value &&
                    seleaten.answer !== item.value &&
                    seleaten.ischecked,
                }"
              >
                <p>{{ item.value }}、{{ item.name }}</p>
              </div>
            </div>

            <!-- 多选题区域 -->
            <div v-if="seleaten.sole === '多选题'">
              <div
                v-for="(item, index) in seleaten.arr"
                :key="index"
                class="con_odd"
                @click="moreClick(item)"
                :class="{ active: item.checked }"
              >
                <p>{{ item.value }}、{{ item.name }}</p>
              </div>
            </div>
          </div>
        </div>
        <!-- 下一题 -->
        <div class="nextss" v-if="lastAnser" @click="save">完成</div>
        <div
          class="nextss"
          @click="NextAdd"
          :class="{ disabled: !isnext }"
          v-else
        >
          下一题
        </div>
      </div>
    </template>

    <div v-show="show">
      <div class="bg_log"></div>
      <div class="log_text">
        <div
          class="title"
          :class="titleName == '恭喜您，您答对了!' ? 'success' : 'error'"
        >
          {{ titleName }}
        </div>
        <div class="text">
          <p class="one_p">
            正确答案为<span>{{
              seleaten.result && seleaten.result.join("")
            }}</span>
          </p>
          <div class="one_resu">
            <p v-for="(item, index) in spams" :key="index">{{ item }}</p>
          </div>
          <div class="two_retu" @click="show = false">返回</div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      settime: null,
      active: 0,
      spams: [],
      titleName: "",
      show: false,
      mulList: [
        {
          type: 1,
          sole: "单选题",
          value: null,
          ischecked: false,
          answer: "A",
          topic:
            "1.回复12345政务热线工单的处理结果包含以下几项内容_____。(单选题)",
          arr: [
            { name: "退款金额", value: "A" },
            { name: "用户满意度", value: "B" },
            { name: "故障修复报告", value: "C" },
            { name: "回访用户时间", value: "D" },
          ],
        },
        {
          type: 2,
          sole: "多选题",
          value: null,
          ischecked: false,
          answer: "B",
          topic:
            "1.回复12345政务热线工单的处理结果包含以下几项内容_____。(多选题)",
          arr: [
            { name: "退款金额", value: "A", checked: false },
            { name: "用户满意度", value: "B", checked: false },
            { name: "故障修复报告", value: "C", checked: false },
            { name: "回访用户时间", value: "D", checked: false },
            { name: "用户意见反馈", value: "E", checked: false },
          ],
          result: ["A", "C"],
        },
        {
          type: 3,
          sole: "判断题",
          value: null,
          ischecked: false,
          answer: "B",
          topic:
            "1.回复12345政务热线工单的处理结果包含以下几项内容_____。(多选题)",
          arr: [
            { name: "退款金额", value: "A", checked: false },
            { name: "用户满意度", value: "B", checked: false },
            { name: "故障修复报告", value: "C", checked: false },
            { name: "回访用户时间", value: "D", checked: false },
            { name: "用户意见反馈", value: "E", checked: false },
          ],
          result: ["A", "C"],
        },
      ],
    };
  },
  computed: {
    lastAnser() {
      return this.active == this.mulList.length - 1 && this.seleaten.ischecked;
    },
    seleaten() {
      return this.mulList[this.active];
    },
    isnext() {
      let state = false;
      if (this.seleaten.sole == "多选题") {
        state = this.seleaten.arr.filter((item) => item.checked).length;
      } else {
        state = this.seleaten.value;
      }
      return state;
    },
  },
  methods: {
    // 单选
    SoleAdd(item) {
      if (this.seleaten.ischecked) return;
      if (this.seleaten.value == item.value) {
        this.seleaten.value = null;
      } else {
        this.seleaten.value = item.value;
      }
    },
    //下一步
    NextAdd() {
      if (this.seleaten.checked || !this.isnext) {
        return false;
      }
      //多选事件
      if (this.seleaten.sole == "多选题") {
        let result = this.seleaten.arr.filter((item) => item.checked == true);
        this.show = true;
        result.forEach((item) => {
          this.spams.push(item.value + "、" + item.name);
        });

        if (
          result.map((x) => x.value).toString() ==
          this.seleaten.result.toString()
        ) {
          this.titleName = "恭喜吖,您答对了！";
        } else {
          this.titleName = "很遗憾，您答错了！";
        }
      }
      this.seleaten.ischecked = true;
      if (!this.lastAnser) {
        if (this.settime) {
          clearTimeout(this.settime);
        }
        this.settime = setTimeout(() => {
          this.active += 1;
        }, 1000);
      }
    },
    // 多选
    moreClick(item) {
      item.checked = !item.checked;
    },
    //完成
    save() {
      alert("完成");
      this.$router.go(-1);
    },
  },
};
</script>
<style lang="scss" scoped>
.answer {
  .container {
    padding: 2.375rem 0.875rem 3.8125rem;
    .content {
      width: 100%;
      height: 33.125rem;
      background-color: #fff;
      border-radius: 0.3125rem;
      margin-bottom: 2.8125rem;
      .con_head {
        display: flex;
        justify-content: space-between;
        padding-right: 1.5625rem;
        .title {
          background-image: url("../assets/image/title_icon.png");
          background-position: center;
          background-size: cover;
          width: 7.625rem;
          height: 2.625rem;
          line-height: 2.625rem;
          padding-left: 1.75rem;
          color: #fff;
          margin-top: -0.5rem;
        }
        .score {
          margin-top: 1.1rem;
          font-size: 1.125rem;
          color: #999;
          span {
            color: #d42454;
          }
        }
      }
      .con_text {
        padding: 0rem 1rem;
        color: #333;
        .title_topic {
          margin: 0.9375rem 0rem 1.875rem;
        }
        .con_odd {
          color: #333;
          border: 0.0625rem solid #979797;
          border-radius: 0.3125rem;
          margin-bottom: 1.25rem;
          height: 3.125rem;
          display: flex;
          align-items: center;
          padding-left: 1rem;
        }
        .active {
          color: #fff;
          background-color: #f1727c;
          border: 0;
        }
        .success {
          color: #00862a;
          background-color: #fff;
          border: 1px solid #00862a;
          position: relative;
        }
        .success::before {
          content: "";
          background: url("../assets/image/success_icon.png") no-repeat;
          position: absolute;
          display: inline-block;
          height: 1rem;
          width: 1rem;
          top: 57%;
          transform: translateY(-50%);
          background-color: #fff;
          padding: 0.1875rem;
        }
        .error {
          border: 1px solid #d81e06;
          color: red;
          background-color: #fff;
          position: relative;
        }
        .error::before {
          content: "";
          background: url("../assets/image/error_icon.png") no-repeat;
          position: absolute;
          display: inline-block;
          height: 1rem;
          width: 1rem;
          top: 57%;
          transform: translateY(-50%);
          background-color: #fff;
          padding: 0.1875rem;
        }
      }
    }
    .nextss {
      width: 18.0625rem;
      height: 3.125rem;
      border-radius: 0.3125rem;
      color: #fff;
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0 auto;
      background: #d42454;
      position: sticky;
      bottom: 0;
    }

    .disabled {
      background: #efafc0;
    }
  }
  .bg_log::before {
    content: "";
    width: 100%;
    height: 100%;
    background-color: #979797;
    opacity: 0.6;
    top: 0;
    left: 0;
    position: fixed;
  }
  .log_text {
    position: absolute;
    top: 51%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    height: 22.25rem;
    width: 80%;
    border-radius: 0.3125rem;
    .title {
      padding: 1.875rem 0rem 1.5625rem;
      border-bottom: 2px solid #ececec;
      font-size: 1.25rem;
      text-align: center;
    }

    .error {
      color: #f1727c;
    }
    .success {
      color: #00862a;
    }
    .text {
      text-align: center;
      padding-top: 1.5625rem;
      .one_p {
        font-size: 1.25rem;
        color: #00862a;
        padding-bottom: 1.6875rem;
      }
      .one_resu {
        margin-left: 3.25rem;
        color: #333333;
        text-align: left;
        p {
          width: 100%;
        }
      }
      .two_retu {
        height: 3.125rem;
        width: 9.375rem;
        display: inline-block;
        text-align: center;
        line-height: 3.125rem;
        color: #fff;
        font-size: 1.25rem;
        background: #d42454;
        border-radius: 5px;
        margin-top: 3.3125rem;
      }
    }
  }
}
</style>
